<template>
    <div class="timer" v-bind:class="{'add-margin': testStep == 4}">
        <div class="time-line"></div>
    </div>
</template>

<script>

export default {
    name: 'Timer',
    computed: {
        testStep: function () {
            return this.$store.state.testStep;
        },      
    },
    props: {
        endTime: Object
    },
    data: () => ({
        timer: null
    }),
    created: function(){
        this.createTimer();
    },
    destroyed: function(){
        this.clearTimerDestroy();
    },
    methods: {
        timerFunc(){
            this.endTime();
            clearTimeout(this.timer);
            this.timer = null;
        },
        createTimer(){
            this.timer = window.setTimeout(this.timerFunc.bind(this), 30000);
        },
        clearTimerDestroy(){
            clearTimeout(this.timer);
            this.timer = null;
        }
    }
}
</script>

<style scoped>
@keyframes timeline {
    from {
        width: 100%;
    }
    to {
        width: 0%;
    }
}
.timer {
    height: 30px;
    background-color: #7d7eab;
    margin-top: 73px;
}
.time-line {
    height: 30px;
    animation: timeline 30s linear;
    background: #c3d600;
}
.add-margin {
    margin-top: 150px;
}
</style>