<script>

export default {
    name: 'Services',
    data: () => ({
        currentServ: 0,
    }),
    methods: {
        changeStep(step, num, service) {
            window.localStorage.setItem('service', service);
            this.currentServ = num;
            setTimeout(function change(){
                this.$store.state.simulatorGraph = step;
                this.$store.state.simulatorStep = 4;
                this.$store.state.service = service;
                this.$store.state.currentPage = 'simulateur';
            }.bind(this), 500)
            
        },
        showDetails(num) {
            this.$store.state.currentPage = 'bibliotheque';
            this.$store.state.libraryDetail = num;
        }
    }
}
