    width: 270px;
    height: 160px;
    color: white;
    display: flex;
    justify-content: center;
    font-size: 26px;
    align-items: center;
}
    .unactive {
    position: relative;

    }
    .header__logo img{
        width: 230px;
    }
    .active-media {
        animation: button;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-direction: alternate;
    }
    .unactive:before {
        display: block;
        content: '';
