        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.6705882352941176);
        z-index: 99999;
        position: absolute;
    }
    .header {
        padding: 0 0 0 30px;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
        right: 0;
        opacity: 1;
        height: 160px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #040c61;
        animation-name: header;
