<template>
    <div v-bind:class="{'unactivs' : simulatorStep == 1}">
        <div class="timeline">
            <div class="timeline-head">
                <span class="bolder">TIMELINE</span>>>>  Faites glisser les années de vie de votre radar pour visualiser son cycle de vie.
                <button class="play-btn" @click="readYears">{{stateAnimeLine == 'play' ? 'JOUER' : 'PAUSE'}}</button>
            </div>
             <div class="tracker-item" >
                <VueDraggableResizable id="impo" :parent="true" :w="920" :h="165" class="item" :resizable="true"  axis="x" :handles="['ml','mr']" v-on:dragging="onDrag" v-on:resizing="onResize">
                    {{timeStep}} - 
                    {{timeStepEnd}}
                    <div class="line"  v-bind:style="{left: ((timeStep - timeStepStart) * (width / (timeStepEnd - timeStepStart))) + 'px'}"/>
                </VueDraggableResizable>
            </div>
            <div v-if="messageSimulateur === true">
                Pause
            </div>
        </div>
        <div class="timeline-footer">
            <span class="visible">{{timeStep}} {{timeStep == 1 ? 'années ' : 'années '}}| {{currentPrase}}</span>
        </div>
    </div>
</template>

<script>
import VueDraggableResizable from 'vue-draggable-resizable';

export default {
    name: 'TimeLine',
    data: ()=> ({
        timeStep: 0,
        stateAnimeLine: 'play',
        intervalID: null,
        timeStepEnd: 0,
        width: 0,
        height: 165,
        x: 0,
        y: 0
    }),
    components: {
        VueDraggableResizable
    },
    computed: {
        currentGraph: function(){
            return this.$store.state.simulatorGraph;
        },
        currentData: function(){
            var data = window.localStorage.getItem(this.currentGraph);
            var result = JSON.parse(data);
            return result;
        },
        year: function() {
            return "year" + this.timeStep;
        },
        currentPrase: function () {
            if (this.currentData) {
                var obj = this.currentData[8];
                return obj[this.year];
            }
        },
        simulatorStep : function() {
            return this.$store.state.simulatorStep;
        },
        messageSimulateur: function() {
            return this.$store.state.messageSimulateur
        }
    },
    methods: {
        changeValue: function() {
            if(this.$store.state.simulatorStep !== 5){
                this.$store.state.simulatorStep = 5;
            }
            this.$store.state.currentYear = this.year;
        },
        onResize: function (x, y, width) {
            this.x = x
            this.y = y
            this.width = width
            // this.height = height
            this.timeStepStart = Math.ceil(this.x / 1970 * 30)
            this.timeStepEnd = Math.ceil((this.x + this.width) / 1970 * 30);
            this.timeStep = Math.ceil(this.x / 1970 * 30);
            this.$store.state.currentYear = 'year' + this.timeStep;
            },
        onDrag: function (x, y) {
            this.x = x;
            this.y = y;
            this.timeStepStart = Math.ceil(this.x / 1970 * 30)
            this.timeStepEnd = Math.ceil((this.x + this.width) / 1970 * 30);
            this.timeStep = Math.ceil(this.x / 1970 * 30);
            this.$store.state.currentYear = 'year' + this.timeStep;
            if(this.$store.state.simulatorStep !== 5){
                this.$store.state.simulatorStep = 5;
            }
        },
        interval () {
            if(+this.timeStep < this.timeStepEnd) {
                if (this.$store.state.messageSimulateur === true) {
                    this.stateAnimeLine = 'play';
                    this.$store.state.messageSimulateur = false;
                    clearInterval(this.intervalID);
                    this.intervalID = null;
                } else {
                    this.stateAnimeLine = 'pause'
                    this.timeStep = +this.timeStep + 1;
                    this.$store.state.currentYear = 'year' + this.timeStep;
                }
            } else {
                if(+this.timeStep == 30 || +this.timeStep == +this.timeStepEnd){
                    this.$store.state.graphType = 'line';
                    this.timeStep = Math.ceil(this.x / 1970 * 30);
                }
                clearInterval(this.intervalID);
                this.intervalID = null;
                this.stateAnimeLine = 'play';
            }
        },
        readYears() {
            if(this.intervalID !== null) {
                clearInterval(this.intervalID);
                this.intervalID = null;
                this.stateAnimeLine = 'play';
            } else {
                if(this.$store.state.graphType == 'line'){
                    this.$store.state.graphType = 'column';
                }
                this.intervalID = setInterval(this.interval.bind(this), 2000);
                this.$store.state.simulatorStep = 5;
                this.stateAnimeLine = 'pause';
            }
        }
    }
}
</script>

