<template>
    <div class="window" v-bind:class="{bad : result == 'bad', right : result == 'right'}">
        <div class="close-btn" @click="nextStep">
            <img src="../../assets/close.png">
        </div>
        <div class="title">
            <div v-if="result == 'right'" class="title-content">
                VRAI
            </div>
            <div v-if="result == 'bad'" class="title-content">FAUX</div>
            <div class="radar">
                <img src="../../assets/small-radar.png">
            </div>
        </div>
        <div class="subtitle" v-if="result == 'right'" v-html="rightTitle"></div>
        <div class="subtitle" v-if="result == 'bad'" v-html="badTitle"></div>
        <div class="message" v-if="result == 'bad'" v-html="badContent">
          
        </div>
         <div class="message" v-if="result == 'right'" v-html="rightContent">
            
        </div>
    </div>
</template>

<script>

export default {
    name: 'AnswerResult',
    props: {
        result: String,
        step: Number,
        nextStep: Object
    },
    computed: {
        badTitle: function(){
            return this.$store.state.questions[this.step].bad_answer_title;
        },
        rightTitle: function () {
            return this.$store.state.questions[this.step].right_answer_title;
        },
        badContent: function () {
            return this.$store.state.questions[this.step].bad_answer_content;
        },
        rightContent: function () {
            return this.$store.state.questions[this.step].right_answer_content;
        }
    }
}
</script>

