<script>
import Exemples from './Exemples.vue';
import Introduction from './Introduction.vue';
import Niveau0 from './Niveau0.vue';
import Niveau1 from './Niveau1.vue';
import Niveau2 from './Niveau2.vue';
import Niveau3 from './Niveau3.vue';


export default {
    name: 'Bibliotheque',
    data: () => ({
        currentSection: 'intro',
    }),
    created: function() {
        this.$store.state.activeNavigate = 'simolator';
    },
    computed: {
        detail: function() {
            if(this.$store.state.libraryDetail) {
                this.changeSection(this.$store.state.libraryDetail)
                return false;
            }
            return this.$store.state.libraryDetail
        }
    },
    components: {
        Exemples,
        Niveau0,
        Niveau1,
        Niveau2,
        Niveau3,
        Introduction
    },
    methods: {
        changeSection(section) {
            this.currentSection = section;
        }
    }
}
</script>

